<template>
  <svg>
    <defs>
      <marker 
					id="arrow" 
					orient="auto"
					markerWidth="6" markerHeight="20"
					refX="3" refY="0">
        <path 
						d="M3,0 L3,10" 
						class="load-arrow"
						marker-end="url(#head)"
				/>
      </marker>
    </defs>

    <path 
				:id="'path' + element.id" 
				class="path-element" 
				:d="elementPathDefinition"
		/>
    <path 
				v-show="element.w !== 0 && view.elementLoads" 
				:id="'pathw' + element.id" 
				class="path-element-load" 
				:d="wPathDefinition"
		/>
		<path 
				v-show="view.momentDiagrams" 
				class="path-moment-diagram" 
				:d="momentDiagram"
		/>

    <circle 
				class="circle-release" 
				v-show="!element.startRigid" 
				:cx="xStartOffset" 
				:cy="yStartOffset" 
				r="2"
		/>
    <circle 
				class="circle-release" 
				v-show="!element.endRigid" 
				:cx="xEndOffset" 
				:cy="yEndOffset" 
				r="2"
		/>

    <text 
				class="select-all"
				:transform="transformList" 
				v-show="view.elementForces"
		>
      <textPath 
					:href="'#path' + element.id" 
					startOffset="50%" 
					text-anchor="middle"
			>
        <tspan dy="-.25em">{{ forceRounded }}</tspan>
      </textPath>
    </text>

    <text 
				class="select-all"
				v-show="element.w !== 0 && view.elementLoads">
      <textPath 
					:href="'#pathw' + element.id" 
					startOffset="50%" 
					text-anchor="middle"
			>
        <tspan class="load-text" dy="-0.25em">{{ Math.abs(element.w) }}</tspan>
      </textPath>
    </text>

  </svg>
</template>

<script>
import math from 'mathjs'

export default {
  name: 'ElementPath',
	props: ['element', 'end-forces', 'element-force', 'view', 'moment-scale'],
	computed: {
		l () {
			return Math.sqrt(Math.pow(this.element.end.x - this.element.start.x, 2) + 
					Math.pow(this.element.end.y - this.element.start.y, 2));
		},
		xStart () {
			return this.view.x + this.view.scale * this.element.start.x;
		},
		yStart () {
			return this.view.y - this.view.scale * this.element.start.y;
		},
		xEnd () {
			return this.view.x + this.view.scale * this.element.end.x;
		},
		yEnd () {
			return this.view.y - this.view.scale * this.element.end.y;
		},
		elementPathDefinition() {
			return 'M' + (this.xStart) + ',' + (this.yStart) 
					+ 'L' + (this.xEnd) + ',' + (this.yEnd);
		},
		wPathDefinition() {
			let str = 'm' + 12 * this.xPerp * Math.sign(this.element.w) + ',' + 12 * this.yPerp * Math.sign(this.element.w)
					+ 'l' + (this.xEnd - this.xStart) / 3 * Math.sign(this.element.w) + ',' + (this.yEnd - this.yStart) / 3 * Math.sign(this.element.w)
					+ 'l' + (this.xEnd - this.xStart) / 3 * Math.sign(this.element.w) + ',' + (this.yEnd - this.yStart) / 3 * Math.sign(this.element.w)
					+ 'l' + (this.xEnd - this.xStart) / 3 * Math.sign(this.element.w) + ',' + (this.yEnd - this.yStart) / 3 * Math.sign(this.element.w);
			if (Math.sign(this.element.w) === 1) {
				return 'M' + (this.xStart) + ',' + (this.yStart) + str;
			} else {
				return 'M' + (this.xEnd) + ',' + (this.yEnd) + str;
			}
		},
		momentDiagram() {
			return 'M' 
				+ (this.xStart - this.endForces[2] * this.momentScale * this.view.diagramScale * this.xPerp) + ',' 
				+ (this.yStart - this.endForces[2] * this.momentScale * this.view.diagramScale * this.yPerp)
				+ 'Q' 
				+ (this.xMid + (0.5 * this.endForces[1] * this.l - this.endForces[2]) * this.momentScale * this.view.diagramScale * this.xPerp) + ',' 
				+ (this.yMid + (0.5 * this.endForces[1] * this.l - this.endForces[2]) * this.momentScale * this.view.diagramScale * this.yPerp) + ' '
				+ (this.xEnd + (-0.5 * this.element.w * this.l * this.l + this.endForces[1] * this.l - this.endForces[2]) * this.momentScale * this.view.diagramScale * this.xPerp) + ',' 
				+ (this.yEnd + (-0.5 * this.element.w * this.l * this.l + this.endForces[1] * this.l - this.endForces[2]) * this.momentScale * this.view.diagramScale * this.yPerp);
		},
		forceRounded () {
			return (math.equal(this.elementForce, 0)) ? 0 
					: this.elementForce.toPrecision(this.view.significantFigures);
		},	
		topRotate () {
			return (this.element.start.x < this.element.end.x) ? 0 : 180;
		},
		xMid () {
			return (this.xStart + this.xEnd) / 2;
		},
		yMid () {
			return (this.yStart + this.yEnd) / 2;
		},
		xPerp () {
			return (-this.yStart + this.yEnd) / (this.l * this.view.scale);
		},
		yPerp () {
			return (-this.xEnd + this.xStart) / (this.l * this.view.scale);
		},
		xStartOffset () {
			return this.xStart + (this.xEnd - this.xStart) * 0.125;
		},
		yStartOffset () {
			return this.yStart + (this.yEnd - this.yStart) * 0.125;
		},
		xEndOffset () {
			return this.xStart + (this.xEnd - this.xStart) * 0.875;
		},
		yEndOffset () {
			return this.yStart + (this.yEnd - this.yStart) * 0.875;
		},
		transformList () {
			return "rotate(" + this.topRotate + ' ' + this.xMid + ' ' + this.yMid + ')';
		},
	},
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "@/assets/styles/_variables.scss";

.path-element {
	stroke: $dark-warm;
	stroke-width: 2px; 
}
.path-element-load {
	marker-end: url(#arrow);
	marker-mid: url(#arrow);
	stroke: $loads-of-gray; 
	stroke-width: 1px; 
}
.path-moment-diagram {
	fill: none;
	stroke: $loads-of-gray;
}
.circle-release {
	fill: #fff;
	stroke: $dark-warm;
}
.load-arrow {
	stroke: $loads-of-gray; 
	fill: none;
}
.load-text {
	fill: $loads-of-gray; 
}
</style>



